

/*div ここから*/
.fixwidth{
   overflow-x: hidden;
}

.div{
   width: 100%;
}
.div1{
   display: flex; 
   justify-content: center;
   align-items: center;
   width: auto;
}
.Div1{
   display: flex;
   justify-content: center;
   justify-content: center ;
   align-items: center;
   position: relative;  
   right: 65px;
   top: 40px;
}
.Div2{
    display: flex;
   justify-content: center;
   justify-content: center ;
   align-items: center;
   position: relative;  
   right: 65px;
   top: 100px;
}

.div1 img{
    position: relative; 
    left: 80px;
 }
 
 
.div1,.div2{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}


.div1 img,.div2 img{
  width: 450px; 
  /* height: auto; */
  border-radius: 30px;
  position: relative;
}
 
 
/*div ここまで */

/*span ここから*/
.span1{
  display: inline-block;
  padding: 10px;
}
.span2{
    display: inline-block;
    padding: 5px;
    position: relative;
    left: 100px;
  }
  .span3{
    display: inline-block;
    padding: 5px;
    position: relative;
    left: 0px;
  }
  .span4{
    display: inline-block;
    padding: 3px;
    position: relative;
    left: 30px;
  }
  
.span1 .img1, .span1 .img2, .span1 .img3,
  .span1 .img6{
    display: block;
    width: 60px;
    padding: 0;
    margin: 0;
    border-radius: 0;
 }
 .span1 .img4, .span1 .img5{
    display: block;
    width: 40px;
    padding: 0;
    margin: 0;
    border-radius: 0;
 }
 
 
  
