body,ul{
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

/*スライドの外枠*/
.slidewrapper {
  width: 45%;
  height: 210px;
  position: relative;
  overflow: hidden;
  top: 30px;
  left: 400px;
  /* background-color: green; */
  
}

.slide img {
width: 100%;
height: 60%;
overflow: hidden;

}
.slide { /*スライド全体*/
width: 880%;  
height: 300px;
display: flex;
transition: all 0.3s;
overflow: hidden;
position: relative;
right: 280px;
transform: translateX(273px)


};
.slide div { /*スライドの中身*/
width: 100%;
height: 100%;
font-size: 16px;
display: flex;
text-align: center;
justify-content: center;
align-items: center;
/* transform: translateX(273px) */
}
.slide1{/*スライドさせるために必要なクラス*/
  transform: translateX(273px);
}

.slide2{/*スライドさせるために必要なクラス*/
  transform: translateX(-318px);
}
.slide3{/*スライドさせるために必要なクラス*/
  transform: translateX(-910px);
}
.slide4{/*スライドさせるために必要なクラス*/
  transform: translateX(-1508px);
}
.slide5{/*スライドさせるために必要なクラス*/
  transform: translateX(-2116px);
}





/*左右のボタン*/
.next{
  position:absolute;
  width: 15px;
  height: 15px;
  right: 10px;
  bottom:  50%;
  z-index: 10;
  cursor: pointer;
  border-top: solid 3px #000;
  border-right: solid 3px #000;
  -webkit-transform:rotate(45deg) translateY(50%);
  transform: rotate(45deg) translateY(50%);


}
.prev{
  position: absolute;
  width: 15px;
  height: 15px;
  left: 25px;
  bottom: 50%;
  z-index: 10;
  cursor: pointer;
  border-top: solid 3px #000;
  border-right: solid 3px #000;
  -webkit-transform: rotate(-135deg) translateY(-50%);
  transform: rotate(-135deg) translateY(-50%);
}
/*インディケーター*/
.indicator{
width: 100%;
position: absolute;
top: 190px;
left: 20px;
display: flex;
column-gap: 18px;
z-index: 10;
justify-content: center;
align-items: center;
}
.indicator li {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  list-style: none;
  background-color: #fff;
  border: 2px #000 solid;
  cursor: pointer;
  position: relative;
  right: 30px;
  bottom: 3px;
  /* background-color: #000; */

}
 
.li1{
  background-color: #000;
}


.indicator li.active { 
  background-color: #000;
}
.indicator li.notactive {
  background-color: #fff;

}

